import React, { useState, useEffect, useReducer, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { WarningOutlined, MinusCircleOutlined } from "@ant-design/icons";
import "./AddCloud.css";
import {
  Col, Row, message, Tabs, Select, Checkbox, InputNumber, Popover, Input,
  Button,
  Modal,
} from "antd";
import Swal from "sweetalert2";
import * as cloud from "@/api/cloud";
import { genEnCode } from "@/utils/util.js";
import { getCommon } from "@/api/common.js"
const { TabPane } = Tabs;
const { Option } = Select;

const initFormData = {
  product_id: 30,
  config_options: {
    data_center_id: "", // 数据中心id
    package_id: "", // 计算能力
    image_id: "", // 镜像id
    vpc_id: "", //vpc
    security_group_id: "", // 安全组
    hostname: "", // 主机名
    password: "", // 密码
    ssh_key_id: "", // 和密码二选一
    backup_enable: false, // 备份
    panel_enable: false, // 独立面板
    duration_price_id: 1, // 周期
  },
  qty: 1,
  customfield: {
    promo_code: [], // 优惠码
  },
  // 以下参数计算价格的时候删除
  bw_type_id: "", // 网络类型
  cal_group_id: "", // 计算能力tab
};
const reducer = (state, action) => {
  switch (action.type) {
    case "center":
      state.config_options.data_center_id = action.data_center_id;
      return state;
    case "package":
      state.config_options.package_id = action.package_id;
      return state;
    case "image":
      state.config_options.image_id = action.image_id;
      return state;
    case "vpc":
      return {
        ...state,
        config_options: {
          ...state.config_options,
          vpc_id: action.vpc_id,
        }
      };
    case "group":
      return {
        ...state,
        config_options: {
          ...state.config_options,
          security_group_id: action.security_group_id,
        }
      };
    case "hostname":
      return {
        ...state,
        config_options: { ...state.config_options, hostname: action.hostname },
      };
    case "password":
      return {
        ...state,
        config_options: { ...state.config_options, password: action.password },
      };
    case "ssh":
      state.config_options.ssh_key_id = action.ssh_key_id;
      return state;
    case "backup":
      return {
        ...state,
        config_options: {
          ...state.config_options,
          backup_enable: action.backup_enable,
        },
      };
    case "panel":
      return {
        ...state,
        config_options: {
          ...state.config_options,
          panel_enable: action.panel_enable,
        },
      };
    case "duration":
      return {
        ...state,
        config_options: {
          ...state.config_options,
          duration_price_id: action.duration_price_id,
        },
      };
    case "qty":
      return { ...state, qty: action.qty };
    case "network":
      return { ...state, bw_type_id: action.bw_type_id };
    case "cal_group":
      return { ...state, cal_group_id: action.cal_group_id };
    default:
      throw new Error();
  }
};
export default function AddCloud () {
  const navigate = useNavigate();
  // 价格前缀
  const [prefix, setPrefix] = useState('')
  const disRef = useRef();
  const [formData, dispatch] = useReducer(reducer, initFormData);
  const { t } = useTranslation();
  const [loading, setLoading] = useState(false);
  const [visible, setVisible] = useState(false);
  const [timer, setTimer] = useState(null);
  // 数据中心
  const [center, setCenter] = useState([]);
  const [dataCur, setDataCur] = useState(0);
  const [dataName, setDataName] = useState("");
  // 带宽
  const [bw, setBw] = useState([]);
  const [bwIndex, setBwIndex] = useState(0)
  // 计算能力
  const [ptab, setPtab] = useState([]);
  const [plist, setPlist] = useState([]);
  const [cur, setCur] = useState(0);
  // 操作系统
  const [imagelist, setImagelist] = useState([]);
  const [imageName, setImageName] = useState(""); // 当前系统名称：Centos/Windows...
  const [version, setVersion] = useState({}); // 系统版本
  // vpc
  const [vpc, setVpc] = useState([]); // 当前数据中心下面的vpc
  const [vpcTemp, setVpcTemp] = useState([]); // 原始vpc列表数据
  // 安全组
  const [group, setGroup] = useState([]);
  // ssh key
  const [ssh, setSsh] = useState([]);
  // 密码获取ssh
  const [pasType, setPastype] = useState("password");
  // 其他配置
  const [other, setOther] = useState({});
  // 周期
  const [duration, setDuration] = useState([]);
  // 优惠码弹窗
  const [disVisible, setDisVisible] = useState(false);
  const [disInput, setDisinput] = useState("");
  // 优惠码列表
  const [discountList, setDiscountList] = useState([]);
  // 支付方式
  const [payList, setPayList] = useState([]);
  // 当前选择的支付方式
  const [curPay, setCurPay] = useState("");
  // 是否使用余额
  const [balanceChecked, setBalanceChecked] = useState(false);
  const [orderId, setOrderId] = useState("");
  // 轮询剩余时间
  const [time, setTime] = useState(300000); // 5分钟
  // 用户信息
  const [user, setUser] = useState({});
  // 订单详情
  const [orderDetail, setOrderDetail] = useState({});
  // 三方支付的html
  const [payHtml, setPayHtml] = useState("");
  // 是否展示第三方支付
  const [isShowPay, setIsShowPay] = useState(true);
  // 订购参数
  const [packageForm, setPackageForm] = useState({
    product_id: 30,
    config_options: {
      data_center_id: "", // 数据中心id
      package_id: "", // 计算能力
      image_id: "", // 镜像id
      vpc_id: "", //vpc
      security_group_id: "", // 安全组
      hostname: "", // 主机名
      password: "", // 密码
      ssh_key_id: "", // 和密码二选一
      backup_enable: false, // 备份
      panel_enable: false, // 独立面板
      duration_price_id: 3, // 周期
    },
    qty: 1,
    // 以下参数计算价格的时候删除
    bw_type_id: "", // 网络类型
    cal_group_id: "", // 计算能力tab
  });
  // 是否勾选协议
  const [agrre, setAgree] = useState(false);
  const [ischeck, setIscheck] = useState(false);
  // 价格展示
  const [price, setPrice] = useState({});
  // 是否展示验证提示
  const [showTip, setShowTip] = useState({
    hostTip: false,
    pasTip: false,
  });
  // 验证是否通过
  const [verify, setVerify] = useState({
    eStart: false,
    length: false,
    isNumAndLetter: false,
    special: false,
  });

  // 数据中心
  const getDataList = async () => {
    try {
      const res = await cloud.getDataCenter(formData.product_id)
      const data = res.data.data.list
      setCenter(data);
      dispatch({ type: "center", data_center_id: data[0].area[0].id })
      setDataName(data && data[0].area[0].area)
    } catch (error) {
      message.error(error.data.msg)
    }
  };
  // 网络类型
  const getBwTypeList = async () => {
    try {
      const res = await cloud.getBwType(formData.product_id);
      const data = res.data.data.list;
      setBw(data);
      dispatch({ type: "network", bw_type_id: data && data[0].id })
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 计算能力(依赖：data_center_id，bw_type_id，cal_group_id)
  const getPackageList = async (
    { centerId, bwId, groupId, imageId } = {},
    bol = false,
    groupBol = true
  ) => {
    try {
      const {
        product_id,
        cal_group_id,
        bw_type_id,
        config_options: { data_center_id },
      } = formData;
      const params = {
        product_id,
        cal_group_id: groupId ? groupId : cal_group_id,
        bw_type_id: bwId ? bwId : bw_type_id,
        data_center_id: centerId ? centerId : data_center_id,
      }
      const res = await cloud.getPackage(params)
      const data = res.data.data
      setPtab(data.cal_group)
      setPlist(data.package)
      if (groupBol) {
        dispatch({ type: "cal_group", cal_group_id: data.cal_group[0]?.id })
      }
      dispatch({ type: "package", package_id: data.package[0]?.id })
      setCur(0)
      // 套餐改变导致价格周期改变
      if (bol) {
        getDurationList(
          {
            packageId: data.package.length > 0 ? data.package[0].id : "",
            backup: formData.config_options.backup_enable,
            panel: formData.config_options.panel_enable,
            image: imageId !== undefined ? imageId : formData.config_options.image_id
          },
          true
        );
        // calcPrice({ package_id: data.package.length > 0 ? data.package[0].id : '' })
      }
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 镜像
  const getSystemList = async ({ centerId } = {}, bol = false) => {
    try {
      const {
        product_id,
        config_options: { data_center_id },
      } = formData;
      const params = {
        product_id,
        data_center_id: centerId ? centerId : data_center_id,
      };
      const res = await cloud.getSystem(params);
      const data = res.data.data.list;
      setImagelist(data);
      setImageName(data[0].name);
      setVersion({
        name: data[0].image[0].name,
        price: data[0].image[0].price
      });
      dispatch({ type: "image", image_id: data[0].image[0].id });
      // 页面操作改变，拉取新的套餐
      if (bol) {
        //  calcPrice({ image_id: data[0].image[0].id });
        getPackageList({
          imageId: data[0].image[0].id,
          groupId: ptab[0].id
        }, true)
      }
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 网络与安全组
  const getVpcList = async () => {
    try {
      const res = await cloud.getVpc({
        page: 1,
        limit: 1000,
      });
      const data_id = formData.config_options.data_center_id
      setVpcTemp(res.data.data.list)
      const temp = res.data.data.list.filter(item => item.id === data_id)
      setVpc(temp.length > 0 ? temp[0].vpc : [])
    } catch (error) {
      message.error(error.data.msg)
    }
  };
  // 根据数据中心筛选符合当前条件的vpc
  const filterVpc = (id) => {
    dispatch({ type: "vpc", vpc_id: '' });
    setVpc([])
    const temp = vpcTemp.filter(item => item.id * 1 === id * 1)
    setVpc(temp.length > 0 ? temp[0].vpc : [])
  }
  const getGroupList = async () => {
    try {
      const res = await cloud.getGroup({
        page: 1,
        limit: 1000,
      });
      setGroup(res.data.data.list)
    } catch (error) {
      message.error(error.data.msg)
    }
  };
  // ssh
  const getSshkeyList = async () => {
    try {
      const res = await cloud.getSshkey({
        page: 1,
        limit: 1000,
      });
      setSsh(res.data.data.list)
    } catch (error) {
      message.error(error.data.msg)
    }
  };
  // 价格周期：package_id，image_id，backup_enable，panel_enable
  const getDurationList = async (
    { packageId, image, backup, panel } = {},
    bol = false
  ) => {
    const {
      config_options: { package_id, image_id, backup_enable, panel_enable },
    } = formData;
    // 清空优惠码
    clearDiscount()
    const params = {
      package_id: packageId ? packageId : package_id,
      image_id: image ? image : image_id,
      backup_enable: backup !== "undfined" ? backup : backup_enable,
      panel_enable: panel !== "undfined" ? panel : panel_enable,
    };
    params.backup_enable = params.backup_enable ? 1 : 0;
    params.panel_enable = params.panel_enable ? 1 : 0;
    try {
      const res = await cloud.getDurationPprice(params);
      const data = res.data.data.list;
      setDuration(data);
      dispatch({ type: "duration", duration_price_id: data[0].id });
      // 周期改变重新计算价格
      if (bol) {
        calcPrice({
          package_id: params.package_id,
          image_id: params.image_id,
          backup_enable: params.backup_enable,
          panel_enable: params.panel_enable,
          duration_price_id: data[0].id,
        });
      }
    } catch (error) {
      console.log('price', error)
      message.error(error.data.msg);
    }
  };
  // 获取其他配置
  const getOtherConfig = async () => {
    try {
      const res = await cloud.getSetting(30);
      const data = res.data.data;
      setOther(data);
      // dispatch({ type: "backup", backup_enable: data.backup_enable ? true : false })
      // dispatch({ type: "panel", panel_enable: data.panel_enable ? true : false })
      // calcPrice({ backup_enable: data.backup_enable ? true : false, panel_enable: data.panel_enable ? true : false })
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 计算价格 依赖计算能力 + 镜像 + 附加服务

  const calcPrice = async ({
    package_id,
    image_id,
    backup_enable,
    panel_enable,
    duration_price_id,
  } = {}) => {
    const params = JSON.parse(JSON.stringify(formData));
    if (!params.config_options.hostname) {
      const hostname = genEnCode(
        Math.floor(Math.random() * 10 + 4),
        1,
        1,
        0,
        1
      );
      const first = [
        String.fromCharCode(Math.floor(Math.random() * 25 + 65)),
        String.fromCharCode(Math.floor(Math.random() * 25 + 97)),
      ];
      const f1 = first[Math.round(Math.random())];
      const result = f1 + hostname;
      dispatch({ type: "hostname", hostname: result });
      params.config_options.hostname = params.config_options.hostname || result;
    }
    if (!params.config_options.password) {
      const password = genEnCode(
        Math.floor(Math.random() * 10 + 4),
        1,
        1,
        0,
        1,
        undefined,
        1
      );
      const p1 = [
        String.fromCharCode(Math.floor(Math.random() * 25 + 65)),
        String.fromCharCode(Math.floor(Math.random() * 25 + 97)),
      ];
      // const s1 = p1[Math.round(Math.random())];
      const result = p1[0] + p1[1] + password + Math.floor(Math.random() * 10);
      dispatch({ type: "password", password: result });
      params.config_options.password = params.config_options.password || result;
    }
    delete params.bw_type_id;
    delete params.cal_group_id;
    if (package_id) {
      params.config_options.package_id = package_id;
    }
    if (image_id) {
      params.config_options.image_id = image_id;
    }
    if (backup_enable !== "undefined") {
      params.config_options.backup_enable = backup_enable;
    }
    if (panel_enable !== "undefined") {
      params.config_options.panel_enable = panel_enable;
    }
    if (duration_price_id) {
      params.config_options.duration_price_id = duration_price_id;
    }
    params.config_options.backup_enable = params.config_options.backup_enable
      ? 1
      : 0;
    params.config_options.panel_enable = params.config_options.panel_enable
      ? 1
      : 0;

    if (pasType === "password") {
      delete params.config_options.ssh_key_id;
    } else {
      delete params.config_options.password;
    }
    try {
      const res = await cloud.updatePrice(params);
      setPrice(res.data.data);
    } catch (error) { }
  };
  // 支付方式
  const getPayWay = async () => {
    try {
      const res = await cloud.payWayList();
      setPayList(res.data.data.list);
      setCurPay(res.data.data.list[0].name);
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 获取用户信息
  const getUser = async () => {
    try {
      const res = await cloud.getUserDetail();
      setUser(res.data.data);
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  useEffect(async () => {
    const res = await getCommon()
    setPrefix(res.data.data.currency_prefix)
    await getDataList();
    await getBwTypeList();
    await getPackageList();
    await getSystemList();
    await getVpcList();
    await getGroupList();
    await getSshkeyList();
    await getOtherConfig();
    await getDurationList();
    await getPayWay();
    await getUser();
    await calcPrice();
    return () => clearInterval(timer);
  }, []);
  /*** 事件操作 ***/
  const create_network = () => {
    navigate("/create_vpc");
  }
  const create_group = () => {
    navigate("/network/security_group");
  }
  /* 数据中心 */
  // 切换tab
  const changeDtab = (e, index) => {
    const id = e.target.getAttribute("data-id");
    setDataCur(index);
    if (id) {
      return false;
    }
    if (index === dataCur) {
      return false;
    }
    dispatch({ type: "center", data_center_id: center[index].area[0].id });
    setDataName(center[index].area[0].area);
    // getPackageList({ centerId: center[index].area[0].id }, true);
    // 数据中心改变： 镜像会刷新，套餐也会刷新，所以先执行刷新镜像，镜像里面再去刷新套餐，再计算价格
    getSystemList({ centerId: center[index].area[0].id }, true, true);
    filterVpc(center[index].area[0].id)
  };
  // 选择具体的id
  const changeDataCenter = (e) => {
    const id = e.target.getAttribute("data-id");
    filterVpc(id)
    dispatch({ type: "center", data_center_id: id });
    setDataName(e.target.getAttribute("data-area"));
    //  getPackageList({ centerId: id }, true);
    getSystemList({ centerId: id }, true, true);
  };
  // 切换带宽
  const changeNet = (e) => {
    const id = e.target.getAttribute("data-id");
    const index = bw.findIndex(item => item.id * 1 === id * 1)
    setBwIndex(index)
    dispatch({ type: "network", bw_type_id: id });
    getPackageList({ bwId: id, groupId: ptab[0].id }, true);
  };
  /* 计算能力 */
  const changePackage = (e) => {
    dispatch({ type: "cal_group", cal_group_id: e });
    setCur(0);
    getPackageList({ groupId: e }, true, false);
  };
  // 切换具体id
  const changePackageId = (id, index) => {
    dispatch({ type: "package", package_id: id });
    setCur(index);
    getDurationList(
      {
        packageId: id,
        backup: formData.config_options.backup_enable,
        panel: formData.config_options.panel_enable,
      },
      true
    );
    // calcPrice({ package_id: id })
  };

  // 切换官方镜像、应用镜像等
  const changeSystem = (e) => {
    console.log(e);
  };
  // 切换系统
  const changeOS = (e, index) => {
    const name = imagelist[index].name// 父元素
    // const test = e.target.getAttribute("data-name");
    setImageName(name);
    // if (!test) {
    //   return false;
    // }
    if (name === imageName) {
      return false;
    }
    const curOS = imagelist[index].image[0];
    dispatch({ type: "image", image_id: curOS.id });
    setVersion({
      name: curOS.name,
      price: curOS.price,
    });
    // 重新计算周期价格
    getDurationList(
      {
        image: curOS.id,
        backup: formData.config_options.backup_enable,
        panel: formData.config_options.panel_enable,
      },
      true
    );
    // calcPrice({ image_id: curOS.id })
  };
  // 选择镜像
  const changeImage = (e) => {
    dispatch({ type: "image", image_id: e.value });
    setVersion({
      name: e.label[0],
      price: e.label[3]
    });
    // 重新计算周期价格
    getDurationList(
      {
        image: e.value,
        backup: formData.config_options.backup_enable,
        panel: formData.config_options.panel_enable,
      },
      true
    );
    //  calcPrice({ image_id: e.value })
  };
  // 选择vpc
  const changeVpc = (e) => {
    dispatch({ type: "vpc", vpc_id: e });
  };
  // 选择安全组
  const changeGroup = (e) => {
    dispatch({ type: "group", security_group_id: e });
  };
  // 选择ssh
  const changeSsh = (e) => {
    dispatch({ type: "ssh", ssh_key_id: e });
  };
  // 校验函数
  const checkVerify = (type, str) => {
    const test1 = /^[A-Za-z]{1,}/;
    const test2 = /^[A-Za-z0-9]+$/;
    const test3 = /^[A-Za-z0-9]+/;
    const test4 = /['`,;=?+./><"\\:$@|~!#$%)([\]}{^&*_-]/;
    setVerify((verify) => {
      return {
        ...verify,
        eStart: test1.test(str),
        length: str.length >= 5 && str.length <= 15,
        isNumAndLetter: type === "hostname" ? test2.test(str) : test3.test(str),
        special: test4.test(str),
      };
    });
  };
  // 生成主机名
  const createHostname = () => {
    const hostname = genEnCode(Math.floor(Math.random() * 10 + 4), 1, 1, 0, 1);
    const first = [
      String.fromCharCode(Math.floor(Math.random() * 25 + 65)),
      String.fromCharCode(Math.floor(Math.random() * 25 + 97)),
    ];
    const f1 = first[Math.round(Math.random())];
    dispatch({ type: "hostname", hostname: f1 + hostname });
  };
  const focusHostname = (e) => {
    setShowTip((showTip) => {
      return { ...showTip, hostTip: true };
    });
    checkVerify("hostname", e.target.value);
  };
  const blurHostname = (e) => {
    setShowTip((showTip) => {
      return { ...showTip, hostTip: false };
    });
  };
  const changeHostname = (e) => {
    dispatch({ type: "hostname", hostname: e.target.value });
    checkVerify("hostname", e.target.value);
  };
  // 生成随机密码
  const createPassword = () => {
    const password = genEnCode(
      Math.floor(Math.random() * 10 + 4),
      1,
      1,
      0,
      1,
      undefined,
      1
    );
    const first = [
      String.fromCharCode(Math.floor(Math.random() * 25 + 65)),
      String.fromCharCode(Math.floor(Math.random() * 25 + 97)),
    ];
    // const f1 = first[Math.round(Math.random())];
    dispatch({
      type: "password",
      password: first[0] + first[1] + password + Math.floor(Math.random() * 10),
    });
  };
  const focusPassword = (e) => {
    setShowTip((showTip) => {
      return { ...showTip, pasTip: true };
    });
    checkVerify("password", e.target.value);
  };
  const blurPassword = (e) => {
    setShowTip((showTip) => {
      return { ...showTip, pasTip: false };
    });
  };
  const changePassword = (e) => {
    dispatch({ type: "password", password: e.target.value });
    checkVerify("password", e.target.value);
  };
  // 切换密码和ssh key
  const changeType = (e) => {
    setPastype(e);
  };
  // 附加服务
  const checkBackup = (e) => {
    dispatch({ type: "backup", backup_enable: e.target.checked });
    // 重新计算周期价格
    getDurationList(
      { backup: e.target.checked, panel: formData.config_options.panel_enable },
      true
    );
    // calcPrice({ backup_enable: e.target.checked, panel_enable: formData.config_options.panel_enable })
  };
  const checkPanel = (e) => {
    dispatch({ type: "panel", panel_enable: e.target.checked });
    // 重新计算周期价格
    getDurationList(
      {
        backup: formData.config_options.backup_enable,
        panel: e.target.checked,
      },
      true
    );
    // calcPrice({ backup_enable: formData.config_options.backup_enable, panel_enable: e.target.checked })
  };
  // 数量
  const changeQty = (e) => {
    dispatch({ type: "qty", qty: e });
  };
  // const enterQty = (e) => {
  //   dispatch({ type: "qty", qty: e.target.value });
  // };
  // 切换周期
  const changeDuration = (e) => {
    dispatch({ type: "duration", duration_price_id: e });
    calcPrice({
      duration_price_id: e,
      panel_enable: formData.config_options.panel_enable,
      backup_enable: formData.config_options.backup_enable,
    });
  };
  // 勾选协议
  const changeCheck = (e) => {
    setIscheck(e.target.checked);
    if (e.target.checked) {
      setAgree(false);
    }
  };
  // 优惠码
  const closeDiscount = async () => {
    try {
      const value = disRef.current.input.value;
      if (!value) {
        return false;
      }
      // 不能相同优惠码
      if (discountList.find(item => item.name === value)) {
        return message.error(t("cloud.tip31"));
      }
      // 使用过的优惠码包含不可叠加的
      if (discountList.find(item => !item.overlay)) {
        return message.error(t("cloud.tip32"))
      }
      const res = await cloud.usePromoCode({
        promo_code: value,
        scene: "New",
        total: Number(price.price),
        cycles: [
          {
            product_id: formData.product_id,
            amount: price.price,
            billing_cycle_time: price.duration,
            config_options: {
              package_id: formData.config_options.package_id,
            }
          },
        ],
      });
      if (res.data.status === 200) {
        const { overlay, discount } = res.data.data
        // 如果之前有使用过优惠码，当前优惠码不可叠加的时候不能使用
        if (discountList.length > 0 && !overlay) {
          return message.error(t("cloud.tip32"))
        }
        setDiscountList((discountList) => {
          const temp = discountList;
          discountList.push({ name: value, num: discount, overlay });
          return temp;
        });
        setDisVisible(false);
      }
    } catch (error) {
      message.error(error);
    }
  };
  const handleVisibleChange = (newVisible) => {
    setDisVisible(newVisible);
    setDisinput("");
  };
  const changeInput = (e) => {
    setDisinput(e.target.value);
  };
  // 取消优惠码
  const cancelDiscount = (index) => {
    setDiscountList((discountList) => {
      const temp = JSON.parse(JSON.stringify(discountList));
      temp.splice(index, 1);
      return temp;
    });
  };
  // 清空优惠码
  const clearDiscount = () => {
    setDiscountList([])
  }
  // 结账
  const checkOut = async () => {
    try {
      if (!ischeck) {
        setAgree(true);
        return false;
      }
      const params = JSON.parse(JSON.stringify(formData));
      params.config_options.backup_enable = params.config_options.backup_enable ? 1 : 0;
      params.config_options.panel_enable = params.config_options.panel_enable ? 1 : 0;
      if (pasType === "password") {
        delete params.config_options.ssh_key_id;
      } else {
        delete params.config_options.password;
      }
      const temp = discountList.length > 0 ? discountList.map((item) => item.name) : [];
      //params.promo_code = temp;
      params.customfield = {
        promo_code: temp
      }
      const res = await cloud.settleProduct(params);
      const id = res.data.data.order_id;
      setVisible(true);
      setOrderId(id);
      // 生成订单开始拉取订单详情，获取状态
      getOrderDetail(id);
      // 进入支付页：默认拉取第一种支付方式
      changePayWay(payList[0].name, id);
      pollingStatus(id);
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 获取订单详情
  const getOrderDetail = async (id) => {
    try {
      const res = await cloud.getOrderDetail(id ? id : orderId);
      setOrderDetail(res.data.data.order);
    } catch (error) {
      message.error(error.data.msg);
    }
  };
  // 切换支付方式
  const changePayWay = async (e, id) => {
    setCurPay(e);
    // 如果账户余额 大于 订单金额 && 勾选了余额支付 阻止调用
    if (
      Number(user.account.credit) >= Number(orderDetail.amount) &&
      balanceChecked
    ) {
      return false;
    }
    try {
      const params = {
        gateway: e,
        id: (typeof id !== "object" && id) || orderId,
      };
      // 使用其他第三方的支付方式
      const res = await cloud.onlinePay(params);
      console.log('@@@', res.data.data.html)
      setPayHtml(res.data.data.html);
    } catch (error) { }
  };
  // 使用余额
  const useBalance = async (e) => {
    setBalanceChecked(e.target.checked);
    if (!e.target.checked) {
      setIsShowPay(true)
    }
    try {
      const res = await cloud.creditPay({
        id: orderId,
        use: e.target.checked ? 1 : 0,
      });
      const tempId = res.data.data.id;
      const result = await cloud.getOrderDetail(tempId);
      const orderRes = result.data.data.order;
      // 勾选余额，如果账户的余额大于订单金额，则使用余额支付
      if (e.target.checked) {
        if (Number(user.account.credit) >= Number(orderRes.amount)) {
          setIsShowPay(false);
        } else {
          setIsShowPay(true);
          changePayWay(curPay, tempId);
        }
      } else {
        // 小于的话重新拉取支付
        changePayWay(curPay, tempId);
      }
    } catch (error) {
      console.log(error);
    }
  };
  // 操作成功弹窗
  const showDialog = (msg) => {
    Swal.fire({
      title: msg,
      icon: "success",
      confirmButtonText: t("common.sure"),
    }).then(() => {
      navigate("/cloud_list", { replace: true });
    });
  };
  // 轮询支付状态
  const pollingStatus = (id) => {
    if (!timer) {
      setTimer(
        (timer) =>
        (timer = setInterval(async () => {
          const res = await cloud.getPayStatus(id ? id : orderId);
          setTime((time) => {
            return (time -= 2000);
          });
          if (res.data.code === "Paid") {
            showDialog(res.data.msg);
            clearInterval(timer);
            setVisible(false);
            return false;
          }
          // 支付成功或者5分钟到点清除定时器
          if (res.code === "Paid" || time === 0) {
            clearInterval(timer);
            setVisible(false);
            return false;
          }
        }, 2000))
      );
    }
  };
  // 通过余额支付
  const handleOk = async () => {
    try {
      const params = {
        gateway: curPay,
        id: orderId,
      };
      await cloud.onlinePay(params);
    } catch (error) { }
  };
  const handleCancel = () => {
    setVisible(false);
    clearInterval(timer);
    navigate("/finance/order", { replace: true });
  };
  return (
    <div className="com-content add-cloud">
      <div className="com-card">
        {/* 数据中心 */}
        <div className="box data-center">
          <div className="com-tit">
            <h1 className="tit">{t("cloud.data_center")}</h1>
          </div>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
            {center.map((item, index) => {
              return (
                <Col
                  xs={{ span: 24 }}
                  md={{ span: 12 }}
                  lg={{ span: 8 }}
                  key={index}
                >
                  <div
                    className={`item c-hover ${index === dataCur ? "c-active" : ""
                      }`}
                    onClick={(e) => changeDtab(e, index)}
                  >
                    <div className="l-area">
                      <img
                        src={require(`@/assets/images/cloud/${item.country_code}.png`)}
                        alt=""
                        className="country"
                      />
                      <span className="txt">{item.city}</span>
                    </div>
                    <div className="r-area">
                      {item.area.map((el, ind) => {
                        return (
                          <span
                            className={`num ${el.id ===
                              formData.config_options.data_center_id * 1 &&
                              index === dataCur
                              ? "cur"
                              : ""
                              }`}
                            key={el.id}
                            data-id={el.id}
                            data-area={el.area}
                            onClick={changeDataCenter}
                          >
                            {el.area}
                          </span>
                        );
                      })}
                    </div>
                  </div>
                  <div className="r-area">
                    {item.area.map((el, ind) => {
                      return (
                        <span
                          className={`num ${el.id ===
                            formData.config_options.data_center_id * 1 &&
                            index === dataCur
                            ? "cur"
                            : ""
                            }`}
                          key={el.id}
                          data-id={el.id}
                          data-area={el.area}
                          onClick={changeDataCenter}
                        >
                          {el.area}
                        </span>
                      );
                    })}
                  </div>
                  <div className="r-area">
                    {item.area.map((el, ind) => {
                      return (
                        <span
                          className={`num ${ind === 0 ? "cur" : ""}`}
                          key={el.id}
                        >
                          {el.area}
                        </span>
                      );
                    })}
                  </div>
                </Col>
              );
            })}
          </Row>
        </div>
        {/* 网络类型 */}
        <div className="box network">
          <div className="com-tit">
            <h1 className="tit">{t("cloud.network_type")}</h1>
            {
              bw.length > 0 && <p className="des">{bw[bwIndex].description}</p>
            }
          </div>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
            {bw.map((item, index) => {
              return (
                <Col
                  xs={{ span: 24 }}
                  md={{ span: 12 }}
                  lg={{ span: 8 }}
                  key={item.id}
                >
                  <div
                    className={`item c-hover ${item.id === formData.bw_type_id * 1 ? "c-active" : ""
                      }`}
                    onClick={changeNet}
                    data-id={item.id}
                  >
                    {item.name}
                  </div>
                </Col>
              );
            })}
          </Row>
        </div>
        {/* 计算能力 */}
        <div className="box">
          <div className="com-tit">
            <h1 className="tit">{t("cloud.compute_power")}</h1>
          </div>
          <div className="package">
            <Tabs
              onChange={changePackage}
              activeKey={String(formData.cal_group_id)}
            >
              {ptab?.map((item) => (
                <TabPane tab={item.name} key={item.id}>
                  <p className="info">{item.description}</p>
                  <Row gutter={{ xs: 8, md: 24, lg: 35 }}>
                    {plist.map((item, index) => {
                      return (
                        <Col
                          xs={{ span: 24 }}
                          md={{ span: 12 }}
                          lg={{ span: 6 }}
                          key={item.id}
                        >
                          <div
                            className={`item c-hover ${index === cur ? "c-active" : ""
                              }`}
                            key={item.id}
                            onClick={() => changePackageId(item.id, index)}
                          >
                            <p className="price">
                              <span className="p-num">
                                {prefix}{item.price.split(".")[0]}
                              </span>
                              <span className="cycle">
                                .{item.price.split(".")[1]}/mo
                              </span>
                            </p>
                            <p className="des" dangerouslySetInnerHTML={{ __html: item.description }}></p>
                          </div>
                        </Col>
                      );
                    })}
                  </Row>
                </TabPane>
              ))}
            </Tabs>
          </div>
        </div>
        {/* 操作系统 */}
        <div className="box">
          <div className="com-tit">
            <h1 className="tit">{t("cloud.operating_system")}</h1>
          </div>
          <div className="system">
            <Tabs onChange={changeSystem}>
              <TabPane tab={t("cloud.official_image")} key="official_image">
                {/* 官方镜像 */}
                <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
                  {imagelist.length > 0 &&
                    imagelist.map((item, index) => {
                      return (
                        <Col
                          xs={{ span: 24 }}
                          md={{ span: 12 }}
                          lg={{ span: 8 }}
                          key={item.id}
                          onClick={(e) => changeOS(e, index)}
                        >
                          <div
                            className={`item c-hover ${imageName === item.name ? "c-active" : ""
                              }`}
                            data-name={item.name}
                          >
                            <div>
                              <img
                                src={require(`@/assets/images/cloud/${item.name}.png`)}
                                alt=""
                                className="image"
                              />
                              <span className="name">{item.name}</span>
                            </div>
                            <Select
                              placeholder={t("cloud.choose_version")}
                              onChange={changeImage}
                              getPopupContainer={triggerNode => triggerNode.parentElement}
                              placement="bottomRight"
                              bordered={false}
                              labelInValue
                              value={{
                                value: formData.config_options.image_id
                                  ? String(formData.config_options.image_id)
                                  : imageName === item.name
                                    ? item.image[0].id
                                    : "",
                                label:
                                  version && imageName === item.name
                                    ? (version.name + ' - ' + prefix + version.price)
                                    : imageName === item.name
                                      ? item.image[0].name
                                      : t("cloud.choose_version"),
                              }}
                            >
                              {item.image.map((el) => {
                                return (
                                  <Option value={el.id} key={el.id}>
                                    {el.name}&nbsp;-&nbsp;{prefix}{el.charge === 1 ? el.price : '0.00'}
                                  </Option>
                                );
                              })}
                            </Select>
                          </div>
                        </Col>
                      );
                    })}
                </Row>
              </TabPane>
              {/* <TabPane tab={t('cloud.apply_image')} key="apply_image">
                Content of Tab Pane 2
              </TabPane>
              <TabPane tab={t("cloud.custom_iso")} key="custom_iso">
                Content of Tab Pane 3
              </TabPane>
              <TabPane tab={t("cloud.clone")} key="clone">
                Content of Tab Pane 3
              </TabPane> */}
            </Tabs>
          </div>
        </div>
        {/* 网络与安全组 */}
        <div className="box network-group">
          <div className="com-tit">
            <h1 className="tit">{t("cloud.network_group")}</h1>
          </div>
          <div className="item">
            <p className="tit">{t("cloud.vpc_network")}</p>
            <div className="select">
              <Select
                placeholder={t("select") + t("cloud.vpc_network")} allowClear
                getPopupContainer={triggerNode => triggerNode.parentElement}
                onChange={changeVpc}
              >
                {vpc.map((item) => {
                  return (
                    <Option value={item.id} key={item.id}>
                      {item.name}
                    </Option>
                  );
                })}
              </Select>
              <div className="create-btn" onClick={create_network}>{t("cloud.create_network")}</div>
            </div>
          </div>
          <div className="item">
            <p className="tit">{t("cloud.security_group")}</p>
            <div className="select">
              <Select
                placeholder={t("select") + t("cloud.security_group")}
                getPopupContainer={triggerNode => triggerNode.parentElement}
                onChange={changeGroup} allowClear
              >
                {group.map((item) => {
                  return (
                    <Option value={item.id} key={item.id}>
                      {item.name}
                    </Option>
                  );
                })}
              </Select>
              <div className="create-btn" onClick={create_group}>
                {t("cloud.create_security_group")}
              </div>
            </div>
          </div>
        </div>
        {/* 配置 */}
        <div className="box network-group config">
          <div className="com-tit">
            <h1 className="tit">{t("cloud.config")}</h1>
          </div>
          <div className="item">
            <p className="tit">{t("cloud.host_name")}</p>
            <div className="select">
              <Input
                value={formData.config_options.hostname}
                onFocus={focusHostname}
                onBlur={blurHostname}
                onChange={changeHostname}
              ></Input>
              <div className="gray-btn" onClick={createHostname}>
                {t("cloud.auto_generate")}
                <div className={`tip ${showTip.hostTip ? "show" : ""}`}>
                  <p className={verify.eStart ? "cur" : ""}>
                    {t("cloud.verify1")}
                  </p>
                  <p className={verify.length ? "cur" : ""}>
                    {t("cloud.length")}5-15
                  </p>
                  <p className={verify.isNumAndLetter ? "cur" : ""}>
                    {t("cloud.verify2")}
                  </p>
                </div>
              </div>
            </div>
          </div>
          {pasType === "password" ? (
            <div className="item">
              <p className="tit">{t("cloud.password")}</p>
              <div className="select">
                <Input
                  value={formData.config_options.password}
                  onFocus={focusPassword}
                  onBlur={blurPassword}
                  onChange={changePassword}
                ></Input>
                <div className="gray-btn" onClick={createPassword}>
                  {t("cloud.random_generate")}
                  <div className={`tip ${showTip.pasTip ? "show" : ""}`}>
                    <p className={verify.eStart ? "cur" : ""}>
                      {t("cloud.verify1")}
                    </p>
                    <p className={verify.length ? "cur" : ""}>
                      {t("cloud.length")}5-15
                    </p>
                    <p className={verify.isNumAndLetter ? "cur" : ""}>
                      {t("cloud.verify2")}
                    </p>
                    <p className={verify.special ? "cur" : ""}>
                      {t("cloud.verify3")}
                    </p>
                  </div>
                </div>
                <div
                  className="create-btn"
                  onClick={() => changeType("ssh_key_id")}
                >
                  {t("cloud.use_ssh_key")}
                </div>
              </div>
            </div>
          ) : (
            <div className="item">
              <p className="tit">SSH KEY</p>
              <div className="select">
                <Select
                  placeholder={t("select") + "SSH KEY"}
                  onChange={changeSsh}
                  listHeight={150}
                  getPopupContainer={triggerNode => triggerNode.parentElement}
                >
                  {ssh.map((item) => {
                    return (
                      <Option value={item.id} key={item.id}>
                        {item.name}
                      </Option>
                    );
                  })}
                </Select>
                <div
                  className="create-btn"
                  onClick={() => changeType("password")}
                >
                  {t("cloud.use_password")}
                </div>
              </div>
            </div>
          )}
        </div>
        {/* 附加服务 */}
        {
          <div className="box service">
            <div className="com-tit">
              <h1 className="tit">{t("cloud.add_services")}</h1>
            </div>
            {Number(other.backup_enable) ? (
              <p className="check">
                <Checkbox
                  onChange={checkBackup}
                  checked={formData.config_options.backup_enable}
                >
                  {t("cloud.auto_backup")}&nbsp;
                  <span className="price">{prefix}{other.backup_price}</span>/
                  {t("cloud.month")}
                </Checkbox>
              </p>
            ) : (
              ""
            )}
            {Number(other.panel_enable) ? (
              <p className="check">
                <Checkbox
                  onChange={checkPanel}
                  checked={formData.config_options.panel_enable}
                >
                  {t("cloud.independent_control_panel")}&nbsp;
                  <span className="price">{prefix}{other.panel_price}</span>/
                  {t("cloud.month")}
                </Checkbox>
              </p>
            ) : (
              ""
            )}
          </div>
        }
      </div>
      {/* 底部汇总 */}
      <div className="total-box">
        <div className="content">
          <div className="item">
            <div className="l-config">
              <div className="s-item">
                <p className="tit">{t("cloud.area")}：</p>
                {center.length > 0 && (
                  <p className="des">
                    <img
                      src={require(`@/assets/images/cloud/${center[dataCur].country_code}.png`)}
                      alt=""
                      className="country"
                    />
                    <span>
                      {center[dataCur].country}/{center[dataCur].city} &nbsp;
                      {dataName}
                    </span>
                  </p>
                )}
              </div>
              <div className="s-item">
                <p className="tit">{t("cloud.system")}：</p>
                {imageName && (
                  <p className="des">
                    <img
                      src={require(`@/assets/images/cloud/${imageName}.png`)}
                      alt=""
                      className="os"
                    />
                    <span>{version.name}&nbsp;-&nbsp;{prefix}{version.price}</span>
                  </p>
                )}
              </div>
            </div>
            <div className="config">
              <p className="tit">{t("cloud.config")}：</p>
              <p dangerouslySetInnerHTML={{
                __html: plist.filter((item) => {
                  return item.id === formData.config_options.package_id * 1;
                })[0]?.description
              }}>
              </p>
            </div>
          </div>
          <div className="item">
            <div className="l-config">
              <div className="s-item">
                <p className="tit">{t("common.number")}：</p>
                <InputNumber
                  min={1}
                  max={999}
                  value={formData.qty}
                  onChange={changeQty}
                />
              </div>
            </div>
            {/* 底部周期价格 */}
            <div className="config">
              <p className="tit">{t("common.cycle")}：</p>
              <div id="duration">
                <Select
                  onChange={changeDuration}
                  getPopupContainer={triggerNode => triggerNode.parentElement}
                  value={formData.config_options.duration_price_id}
                  optionLabelProp="label"
                >
                  {duration.map((item) => {
                    return (
                      <Option value={item.id} key={item.id} label={prefix + item.price + '/' + item.display_name}>
                        {prefix}{item.price}/{item.display_name}
                      </Option>
                    );
                  })}
                </Select>
              </div>
            </div>
          </div>
          <div className="item">
            <div className="l-config">
              <div className="s-item">
                <p className="tit">{t("common.total_price")}：</p>
                {price && (
                  <>
                    <p className="price">
                      ¥
                      <span className="num">
                        {String(
                          (
                            (price.price * formData.qty - discountList.reduce((sum, i) => (sum += i.num * 1), 0)) >= 0 ?
                              (price.price * formData.qty - discountList.reduce((sum, i) => (sum += i.num * 1), 0)) : 0
                          ).toFixed(2)
                        )}
                      </span>
                      /{price.billing_cycle}
                    </p>
                    {/* 优惠码列表 */}
                    <div className="discount-list">
                      {discountList.length > 0 &&
                        discountList.map((item, index) => {
                          return (
                            <p className="item" key={item.name}>
                              <span className="num">-¥{item.num}</span>
                              <span>
                                ({t("common.discount_code") + item.name})
                                <MinusCircleOutlined
                                  onClick={() => cancelDiscount(index)}
                                />
                              </span>
                            </p>
                          );
                        })}
                    </div>
                  </>
                )}
              </div>
            </div>
            <div className="config">
              <p className="tit"></p>
              <div className="bot" id="bot">
                <Popover
                  placement="bottomRight"
                  getPopupContainer={() => document.getElementById("bot")}
                  content={
                    <div className="discount">
                      <Input
                        placeholder={t("cloud.input_discount_code")}
                        value={disInput}
                        ref={disRef}
                        onChange={changeInput}
                      ></Input>
                      <span className="close" onClick={closeDiscount}>
                        {t("common.sure")}
                      </span>
                    </div>
                  }
                  trigger="click"
                  visible={disVisible}
                  onVisibleChange={handleVisibleChange}
                >
                  <span className="discount_code">
                    {t("common.discount_code")}
                  </span>
                </Popover>
                <div className="r-btn">
                  <p className="checkout" onClick={checkOut}>
                    {t("common.checkout")}
                  </p>
                  <p>
                    <Checkbox onChange={changeCheck}></Checkbox>
                    <a href="http://www.baidu.com" className="agree" target="_blank">
                      {t("cloud.tip15")}
                    </a>
                    {agrre && (
                      <span
                        style={{
                          color: agrre ? "#f64e60" : "#ccc",
                          display: "block",
                          fontSize: ".12rem",
                        }}
                      >
                        <WarningOutlined />
                        {t("common.check_agree")}
                      </span>
                    )}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* 支付弹窗 */}
      <Modal
        visible={visible}
        title={t("common.pay")}
        onOk={handleOk}
        onCancel={handleCancel}
        className="pay-modal"
        closable={false}
        footer={
          !isShowPay && [
            <Button
              key="submit"
              className="sure"
              loading={loading}
              onClick={handleOk}
            >
              {t("cloud.sure_pay")}
            </Button>,
            <Button key="back" onClick={handleCancel} className="cancel">
              {t("common.cancel")}
            </Button>,
          ]
        }
      >
        <div className="cloud-pay-box">
          <div className="item first">
            <div className="l-text">{t("common.pay_way")}</div>
            <div className="r-text">
              {payList.length > 0 && (
                <Select value={curPay} onChange={changePayWay}
                  getPopupContainer={triggerNode => triggerNode.parentElement}>
                  {payList.map((item) => {
                    return (
                      <Option value={item.name} key={item.id}>
                        {item.title}
                      </Option>
                    );
                  })}
                </Select>
              )}
              <Checkbox onChange={useBalance} checked={balanceChecked}>
                {t("cloud.use_balance")}
              </Checkbox>
            </div>
          </div>
          <div className="item">
            <div className="l-text">{t("cloud.order_amount")}</div>
            <div className="r-text order-num">￥&nbsp;{orderDetail.amount}</div>
          </div>
          <div className="item last">
            <div className="l-text">{t("cloud.pay_amount")}</div>
            <div className="r-text pay-num">
              <p className="paid">
                ￥
                {
                  // 当勾选余额的时候：订单金额 - 账户余额，小于0 则表示全部使用
                  balanceChecked
                    ? (Number(orderDetail.amount) -
                      Number(user.account.credit) <=
                      0
                      ? 0
                      : Number(orderDetail.amount) -
                      Number(user.account.credit)
                    ).toFixed(2)
                    : Number(orderDetail.amount).toFixed(2)
                }
              </p>
              {balanceChecked && (
                <p className="balance">
                  -￥
                  {(Number(user.account.credit) >= Number(orderDetail.amount)
                    ? Number(orderDetail.amount)
                    : Number(user.account.credit)
                  ).toFixed(2)}
                </p>
              )}
            </div>
          </div>
          {
            // 可用余额小于订单金额
            isShowPay && (
              <div className="item pay-html">
                {/* 渲染支付的模块 */}
                {/* <img src={payHtml} alt="" /> */}
                <div dangerouslySetInnerHTML={{ __html: payHtml }}></div>
              </div>
            )
          }
        </div>
      </Modal>
    </div>
  );
}
